/* Full-page container with dark background */
.task-form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh; /* Full viewport height */
  background-color: #000; /* Black background */
  color: #fff;
}

/* Form Styling */
form {
  background-color: #111; /* Slightly lighter black */
  border-radius: 10px;
  padding: 10px;
  width: 100%;
  max-width: 800px; /* Wider for better alignment */
  border: 0px;
}

/* Form Row */
.form-row {
  display: grid;
  grid-template-columns: 1fr 4fr 1fr; /* Proportions for dropdown, input, and button */
  gap: 10px; /* Space between columns */
  margin-bottom: 5px;
}

/* Dropdown Styling */
.priority-dropdown {
  background-color: #333;
  border-radius: 5px;
  font-size: 14px; /* Smaller font size */
  height: 32px; /* Set consistent height */
  width: 120px;
  color: #fff;
  line-height: normal;
}

/* Input Styling */
input[type="text"] {
  width: 98%;
  background-color: #333;
  color: #fff;
  border: 0px;
  border-radius: 5px;
  padding: 5px; /* Reduced padding for smaller height */
  font-size: 14px; /* Smaller font size */
  height: 22px; /* Set consistent height */
}

/* Submit Button */
button[type="submit"] {
  background-color: #555;
  color: #fff;
  padding: 5px; /* Reduced padding for smaller height */
  border: 0px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px; /* Smaller font size */
  height: 32px; /* Set consistent height */
}

button[type="submit"]:hover {
  background-color: #777; /* Slight hover effect */
}

/* Second Row: Align Date-Time Picker to the Right */
.form-row:last-child {
  display: flex;
  justify-content: flex-end; /* Aligns content to the right */
}

/* Limit the width of the Date-Time Picker */
input[type="datetime-local"] {
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px;
  width: auto; /* Set to auto to prevent stretching */
  min-width: 200px; /* Add a reasonable minimum width */
}
