/* Dropdown Container */
.priority-dropdown {
    position: relative;
    display: inline-block;
    cursor: pointer;
    user-select: none;
  }
  
  /* Selected Priority Display */
  .selected-priority {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 5px 10px;
    background-color: #333;
    color: white;
    border-radius: 5px;
    border: 0px;
    width: 100px;
  }
  
/* Dropdown Options - Arrange Horizontally */
.priority-options {
  position: absolute;
  margin-top: 5px;
  background-color: #333;
  border: none;
  border-radius: 5px;
  display: flex; /* Use flexbox for horizontal arrangement */
  gap: 5px; /* Space between options */
  padding: 5px; /* Optional padding for better spacing */
}

/* Priority Option Styling */
.priority-option {
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 5px 10px; /* Adjust padding for a compact look */
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border-radius: 5px; /* Rounded corners for individual options */
}

.priority-option:hover {
  background-color: #444;
}

  
  .priority-option:hover {
    background-color: #444;
  }
  
  /* Priority Circle */
  .priority-circle {
    width: 15px;
    height: 15px;
    border-radius: 50%;
  }
  